        "Title": "Aspirations of young women in Worcester's private schools.",
        "DateCreated": "1/1/2005",
        "ProjectType": "IQP",
        "Availability": "Restricted",
        "Abstract": "The City Manager's Advisory Committee on the Status of Women (ACSW) recruited a WPI project team to gather information regarding the career aspirations of the City of Worcester's youth. A gender-comparative study was then conducted by two student teams which administered the same two-page survey to the public and private/ charter junior year high school students in order to compare the sexes in terms of their interests, concerns and activities related to their career aspirations. This is the report on the private/charter high school students. A statistical analysis produced results that show that Worcester students appear to be making little to no substantial progress towards gender equality and the public-private school differences are not as large as expected. In addition, the Catholic schools surveyed are lagging behind to a greater extent in this area of social change. There are more marked differences in aspirations by sex in those particular high schools than in the others.",
