        "Stakeholders": "Worcester City Manager, Worcester students",
        "Impact": "Not many differences between career aspirations of students in private/charter schools when compared with public schools overall; more females in public schools aspiring to careers in business, medical practice, and law, which are typically male-dominated; more students in private/charter schools interested in college than students in public schools; no correlation between ethnicity and future plans in private/charter schools; more ethnic students had career reservations because people of their gender either did not go into that field or they thought college was too difficult; Catholic schools were far more gender stereotypical in all but two areas",
