        "Abstract": "WPI's Electrical and Computer Engineering program has traditionally maintained a low percentage of female undergraduates. Factors responsible for this underrepresentation are isolation consequent to male dominance, limited portrayal of ECE rewards, low self-confidence and failure to establish ECE interest among women. Featured in this project are research, methods, results and final recommendations. Realistic solutions are based on closing analysis; they include increasing ECE awareness, modifying academic advisor assignment procedure, using tour guides to effectively promote ECE, and increasing female faculty.",
