        "Availability": "Restricted",
        "Abstract": "This Interactive Qualifying Project (IQP) investigated the processing of breast cancer information by college-aged women. Women students at WPI and Becker College were asked to read two articles, one from a news magazine and one from a science magazine, about the possible connection between use of the birth control pill and breast cancer risk later in life. A questionnaire was then administered to probe their understanding, factual recall, and emotional response to each article. Results showed that while there was not a significantly different response to the two articles, Becker and WPI students varied significantly in their opinions of article readability, their feelings after reading the articles, and the conclusions they drew from the articles.",
