        "Title": "Change in Academic Motivation at WPI",
        "DateCreated": "10/24/2011",
        "ProjectType": "IQP",
        "Availability": "unrestricted",
        "Abstract": "This project analyzed the change in academic motivation for 200 students from WPI based on data obtained from the Wabash National Study of Liberal Arts Education (WNS). This report identified factors which influence a student's change in academic motivation and examined the validity of the Academic Motivation Scale in the WNS. Academic motivation decreased over freshman year in these 200 students. It decreased further in the 17 students who retook the scale in 2011. Male students were more likely to decrease in academic motivation than female students. There was a relationship between the change in academic motivation and student-faculty interactions. Intrinsic motivation in the 200 students had a significantly greater decrease than extrinsic motivation.",
