        "Title": "Comparative Aspirations Study on Worcester Secondary Schools",
        "DateCreated": "4/11/2009",
        "ProjectType": "IQP",
        "Availability": "Unrestricted",
        "Abstract": "Career aspirations studies were conducted among 8th and 11th graders in the Worcester schools to determine progress toward gender and ethnic equity.  Medicine, business, and law were considered most likely to achieve gender equity soon.  Technology and political fields will probably remain male dominated for the near future.  Medicine could become female dominated.  Ninth and tenth grade is the critical time when interest, especially female interest, in science and engineering erodes.  Ethnic differences are related to career concerns but not which careers are interesting. Worcester students with technical, arts, health and public service oriented career interests are not concentrating in the high schools with small schools devoted to these fields.",
