        "Identifier": "E-project-121417-014811",
        "Title": "Exploring Intravaginal Ring Acceptability for Disease Prevention Among At-Risk Community Members in Cape Town",
        "DateCreated": "12/14/2017",
        "ProjectType": "IQP",
        "Availability": "unrestricted",
        "Abstract": "South Africa has the highest prevalence of HIV in the world, with young black women at greatest risk of infection. New protective biotechnologies continue to be developed, including an intravaginal ring (IVR) that releases antiretroviral medications over a four-week period. An IVR is currently under development that could protect against HIV, STIs, and pregnancy concurrently. However, uptake and adherence to this product is unknown. This project explored access and acceptability by interviewing 28 at-risk women and men in Cape Town and consulting with five stakeholders. Data revealed that familiarity with similar intravaginal products (tampons, female condoms), relationship dynamics, and product distribution channels must be considered as IVR products undergo further development.",
