        "DateCreated": "10/18/2008",
        "ProjectType": "IQP",
        "Availability": "unrestricted",
        "Abstract": "In the current study, we examined the attitudes students had towards science and investigated whether we could improve attitudes toward science by exposing students to engaging activities towards different areas of science.  To do so, we recreated a 14-week after school science club at a local urban high school.  Prior to participating, we surveyed students on their thoughts of science and engineering. Then, we ran the science club one day a week for 14 weeks.  Each week, we completed a project pertaining to a different area of science and engineering (e.g., building a bridge out of popsicle sticks discussing physics and civil engineering). After the completion of the 14-week period, we surveyed students again.",
