        "Sponsor": "",
        "Date": "8-Oct",
        "ProjectCenter_y": "On-Campus",
        "Location_y": "Worcester, MA",
        "SubjectTerms": "Science--Study and teaching | Women--education--science",
        "Link": "https://digitalcommons.wpi.edu/cgi/viewcontent.cgi?article=1606&context=iqp-all",
        "Advisors_y": "Skorinko, Jeanine L. | Connors, Robert",
        "Initials": "CS",
        "Notes": "",
        "LinkToSummary": "https://docs.google.com/document/d/1_kXrd2I4shZ3U-R8B7CBVlmMBP-6HlFpWgcbLuISanM/edit?usp=sharing",
        "Goal": "To explore whether exposing students to different areas of science would influence their attitudes towards the different areas of science",
        "Stakeholders": "North High School students",
        "Impact": "Students initially preferred biology over chemistry and physics, but after 14 weeks of going to the club, they liked all subjects equally. No gender differences found in preferences towards science, and ttitudes towards physical science improved equally",
