        "Abstract": "Violence against women is a pressing issue in the Broadmeadows community. The main social determinants of violence against women are rigid gender roles and stereotypes, unequal gender relations, and violence-supportive attitudes. These determinants predict higher rates of violence against women. Our project goal was to create resources as a community training tool to expand the Good People Act Now project, supported by Banksia Gardens. Community insight, obtained through observing students and interviewing teachers and Banksia staff, was used to develop training materials. Deliverables included a program for year one students on gender equality and respectful relations, and a training program on violence against women and bystander action for secondary students.",
