        "ProjectType": "IQP",
        "Availability": "Restricted",
        "Abstract": "The London Science Museum is developing two interactives that combine the latest biomedical research with cutting edge face morphing software. The morphing software requires a database of facial images to execute the morphing process. Our project involved developing a strategy to create this image database. To insure that the database was compatible with the interactives, we developed a prototype of the interactives that simulated lighting, backdrop, and camera position. We then implemented strategies to advertise, recruit volunteers, and build the database. The result was a complete database diverse in age, race, and gender. The database will be fully compatible with the interactives, thus creating realistic impressive morphed images. We analysed our methods and made recommendations for people interested in reproducing our work in the future.",
