        "Goal": "To develop an educational web page with info about RU486 and other abortion techniques (including procedural) and the pros and cons of each technique",
        "Stakeholders": "Women",
        "Impact": "Listed three main issues to the abortion debate: status of the embryo, how to access facts surrounding the issue, and the debate about abortion is actually a debate about the role of a person",
