        "Sponsor": "GO2 Albania | Shkreli, Eljana",
        "Date": "12/14/2018",
        "ProjectCenter_y": "Tirana, Albania",
        "Location_y": "Tirana, Albania",
        "SubjectTerms": "Women--Social conditions",
        "Link": "http://www.wpi.edu/Pubs/E-project/Available/E-project-121418-085048/unrestricted/XhubletaBook.pdf",
        "Advisors_y": "Dodson, Leslie L.| Hersh, Robert",
        "Initials": "AN",
        "Notes": "Visually pleasing report",
        "LinkToSummary": "https://docs.google.com/document/d/13XejBnoEziFFCCokvnt71Wi5-8KshN1NXkl8yiLUY6I/edit?usp=sharing",
        "Goal": "To understand and document the changing experience of women in northern Albania over the past century, using a traditional costume, the xhubleta, as a lens to see into their lives",
        "Stakeholders": "GO2 Albania, women in Albania",
        "Impact": "Found contradictions surrounding the various cultural meanings assigned to the dress, with women?s subjugation under patriarchal customs co-existing with women?s pride in wearing it;\nthe dress is seen as an object, unattached to the lives of the wearers., which is very different from the ways the xhubleta was seen in the past",
        "Headline": "Women's perspective on Albanian traditional dress",
        "Follow-up": "",
        "Category": "Cultural"
