        "Title": "Uncorking Identity: Exploring Consumer-Brand Identification within the South African Wine Industry",
        "DateCreated": "12/14/2017",
        "ProjectType": "IQP",
        "Availability": "unrestricted",
        "Abstract": "New wine producers in South Africa struggle to enter the established domestic wine market. This project investigated the importance of identity in wine consumption. We conducted a survey (n=102) to assess drivers of consumer-brand identification, a paired comparison of wine labels (n=59), and interviewed industry experts (n=10). Results indicate that identity does have an influence on consumer wine preferences, with black consumers identifying with labels displaying South African traits. Additionally, brand prestige, social benefits, distinctiveness, and warmth influence wine purchasing preferences more generally. New producers should market their distinct identities (black, female) as they reveal warmth and familiarity to certain consumer segments, and can drive brand distinctiveness.",
        "FullTextUrl": "http://www.wpi.edu/Pubs/E-project/Available/E-project-121417-094228/unrestricted/WIW_Final_IQP_Report_B17.pdf",
