        "DateCreated": "1/1/2003",
        "ProjectType": "IQP",
        "Availability": "Restricted",
        "Abstract": "We conducted a pilot study using existing MBTI data, SAT scores, grades, and college placement data from the Worcester Public School's class of 1999. The findings revealed that intuitive and perceiving high school students had higher SAT averages than sensing and judging high school students. Among females, the feeling students had an advantage on the SAT.  When the SAT was divided into ranges sought by and typical of the more selective colleges, it was clear that the differences by these MBTI variables were large enough to be of practical significance in terms of college admission.  These differences in cognitive distribution sometimes appear in odd ways. For example, two of the four high schools in Worcester have significantly higher percentages of intuitives than the other two schools.  Furthermore, the minority population had lower SAT scores which may may partly be explained by the differences in sensing and intuition by ethhnic distribution in Worcester.",
