        "DateCreated": "3/14/2011",
        "ProjectType": "IQP",
        "Availability": "unrestricted",
        "Abstract": "The present study examined the role of affiliative motivation and perspective taking on social tuning. Eighty-two participants believed that they would be working with a partner for either five (low affiliative condition) or 30 (high affiliative condition) minutes.  Participants also completed a writing task that was either about a friend in need (Perspective taking condition) or on a topic unrelated to perspective taking (No perspective taking condition).  Participants then learned their partner wished to write a debate that supported gender-traditional views.  The results showed that both affiliative motivation and perspective taking influenced individuals to tune, but not more so than if they were only engaging in one form of motivation.",
